<template>
  <div class="home">
    <div class="home-banner">
      <van-swipe class="my-swipe" :show-indicators="false" :autoplay="3000" indicator-color="white">
        <van-swipe-item class="banner-item" v-for="(item,index) in bannerData" :key="index">
          <img class="auto-img" :src="item.pic" />
          <span class="banner-title" :style="{backgroundColor:item.titleColor}">{{item.typeTitle}}</span>
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 用户推荐 -->
    <div class="ball">
      <van-swipe :show-indicators="false" :loop="false" :width="70">
        <van-swipe-item class="ball-item" v-for="(item, index) in ballData" :key="index" @click="godetailpage(item)">
          <div class="ball-img-box">
            <img class="auto-img ball-img" :src="item.iconUrl" />
            <span class="ball-date" v-if="item.id == -1">{{newDate}}</span>
          </div>
          <p class="ball-title">{{item.name}}</p>
        </van-swipe-item>
      </van-swipe>
    </div>

    <div v-if="loveData.trackCount">

    <div class="my-love clearfix" @click="goSongListDetails(loveData)">
      <div class="love-img-box fl">
        <img class="auto-img" :src="loveData.coverImgUrl" />
      </div>
      <div class="love-content fl">
        <div class="love-name">{{loveData.name}}</div>
        <div class="love-count">{{loveData.trackCount}}首</div>
      </div>
      <div class="love-foot fr">
        <img class="auto-img" src="../../assets/images/love-img.png" />心动模式
      </div>
    </div>
    </div>
    <div v-else>
      <div class="my-love clearfix">
      <div class="love-img-box fl">
        <img class="auto-img" src="../../assets/images/collect.png" />
      </div>
      <div class="love-content fl">
        <div class="love-name">我喜欢的音乐</div>
        <div class="love-count">0首</div>
      </div>
      <div class="love-foot fr">
        <img class="auto-img" src="../../assets/images/love-img.png" />心动模式
      </div>
    </div>
    </div>
    

    <div class="resource">
      <div class="resource-top">
        <div>推荐歌单</div>
        <div>歌单广场</div>
      </div>
      <div class="resource-content">
        <van-swipe :loop="false" :show-indicators="false" :width="115">
          <van-swipe-item v-for="(item,index) in personalizedData" :key="index"  @click="goSongListDetails(item)">
            <div class="resource-img-box">
              <img class="auto-img resource-img" :src="item.picUrl" />
              <div class="resource-playcount"><img src="../../assets/images/playcount.png" class="auto-img resource-count" />{{item.playcount | million}}</div>
            </div>
            <p class="resource-text">{{item.name}}</p>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <div class="recommend">
      <van-tabs class="recommend-tab" color="#eee" background="#272727" v-model="recommendcount" animated @change="getRecommend" swipeable>
        <van-tab v-for="(item,index) in recommendTypeData" :title="item.title"  :key="index">
          <div class="recommend-item clearfix" v-for="(item,index) in recommendData" :key="index" @click="goPlayPage(item)">
            <div class="recommend-img-box fl">
              <img :src="item.picUrl" class="auto-img">
            </div>
            <div class="recommend-content fl">
              <div class="recommend-text">{{item.name}}</div>
            </div>
            <div v-show="recommendcount == 1" class="recommend-play fr" @click.stop="setStateCount(item)">
              <img src="../../assets/images/play-img.png" class="auto-img" alt="">
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- <PlayMusic></PlayMusic> -->
  </div>
</template>

<script>
import "../../assets/less/home.less";
import { setupCookie } from "../../assets/js/setupCookie";
// import PlayMusic from '../../components/PlayMusic'
export default {
  data() {
    return {
      bannerData: [],
      personalizedData: [],
      ballData: [],
      newDate: 1,
      loveData: [],
      recommendData:[],
      recommendcount: 0,
      playmusicids:[],
      playmusiclistid:0,
      recommendTypeData:[
        {
          title:'推荐歌单'
        },
        {
          title:'推荐音乐'
        },
        {
          title:'推荐电台'
        }
      ]
    };
  },
  filters: {
    million: function(value) {
      //播放量数值处理
      let num;
      if (value > 99999999) {
        num = Math.floor(value / 10000000) / 10 + "亿";
      } else if (value > 9999) {
        num = Math.floor(value / 1000) / 10 + "万";
      } else if (value < 9999 && value > -9999) {
        num = value;
      }
      return num;
    },
  },
  created() {
    this.gethomebanner();
    this.getRecommendResource();
    this.getUserInfo();
    this.getTodayTime();
    this.getUserData();
    this.getRecommend();
  },
  computed: {
    recommendType:function() {
      if (this.recommendcount == 0) {
        return "/personalized?limit=9";
      } else if (this.recommendcount == 1) {
        return "/personalized/newsong";
      } else if (this.recommendcount == 2) {
        return "/personalized/djprogram";
      } else {
        return "/program/recommend";
      }
    },
  },
  methods: {
    // 获取今日日期
    getTodayTime() {
      this.newDate = new Date().getDate();
    },
    // 获取轮播图
    gethomebanner() {
      this.axios({
        method: "GET",
        // 请求参数
        url: "/banner",
        params: {
          type: 1,
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.bannerData = result.data.banners;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    // 获取推荐歌单
    getRecommendResource() {
      this.axios({
        method: "GET",
        // 请求参数
        // url: "/personalized",
        url: "/recommend/resource",
        params: {
          cookie: setupCookie.getCookies(),
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.personalizedData = result.data.recommend;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //获取圆形图标
    getUserInfo() {
      this.axios({
        method: "GET",
        // 请求参数
        url: "/homepage/dragon/ball",
        params: {
          cookie: setupCookie.getCookies(),
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.ballData = result.data.data;
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    // 获取用户信息，歌单、收藏...
    getUserData() {
      this.axios({
        method: "GET",
        url: "/user/playlist",
        params: {
          uid: localStorage.getItem("USERID"),
        },
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.loveData = result.data.playlist[0];
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
   //获取推荐列表
    getRecommend() {
      
      // // 验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
       this.axios({
        method: "GET",
        url: this.recommendType,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.recommendData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });
    },
    //点击播放歌曲
    setStateCount(item) {
      this.axios({
        method: "GET",
        url: "/song/detail",
        params: {
          ids: item.id,
        }
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.playmusicids = this.recommendData.map(v => {
              return v.id
            })
          }
        })
        .catch((err) => {
          console.error(err)
        });
    },
    //前往详情页面
    goPlayPage(item){
      if (this.recommendcount == 0) {
        this.goSongListDetails(item)

      } else if (this.recommendcount == 1) {
        //获取播放列表
        this.axios({
        method: "GET",
        // url: "/playlist/detail",
        url: "/song/detail",
        params: {
          ids: item.id,
        }
      })
        .then((result) => {
          if (result.data.code === 200) {
            this.playmusicids = this.recommendData.map(v => {
              return v.id
            })
            this.$store.commit("setplaymusicids", this.playmusicids)
          }
        })
        .catch((err) => {
          console.error(err)
        });
        this.$store.commit("setmusicid",item.id)
      }
    },
    //前往歌单详情页面
    goSongListDetails(item){
      this.$router.push({path:'songlistdetails', query:{id:item.id}})
    },
    
    //前往圆形图标
    godetailpage(item){
      if (item.id == -1) {
        this.$router.push({path:'daily'})
      }
    }
  },
  components:{
  }
};
</script>